import LayoutDashboard from '@/components/layout.component';
import NotFoundPage from '@/components/notfound.component';
import UserRouter from '@/modules/user/user.route';
import { lazy } from 'react';
import { Link, useRoutes } from 'react-router-dom';
import AuthRoute from './authRoute.component';

const LoginPage = lazy(() =>
  import(/* webpackChunkName: "with-aside" */ '@/modules/auth/login.container')
);
const UserList = lazy(() =>
  import(/* webpackChunkName: "with-tabs" */ '@/modules/user/user.list')
);

const routeList = [
  {
    path: '/login',
    element: <LoginPage />,
  },
  {
    path: '/user',
    element: <AuthRoute element={<UserList />} titleId="title.login" />,
  },
  {
    path: '/dashboard',
    element: <LayoutDashboard />,
    children: [{ path: 'user/*', element: <UserRouter /> }],
  },
  {
    path: '/dashboardd/*',
    element: <UserRouter />,
  },
  {
    path: '/',
    element: <Link to="/dashboard">Go to user</Link>,
  },

  {
    path: '*',
    element: <NotFoundPage />,
  },
];

const RenderRouter = () => {
  const element = useRoutes(routeList);

  return element;
};

export default RenderRouter;
