import { lazy } from 'react';
import { useRoutes } from 'react-router-dom';

const LayoutDashboard = lazy(() => import('@/components/layout.component'));
const NotFoundPage = lazy(() =>
  import(
    /* webpackChunkName: "notfound.component" */ '@/components/notfound.component'
  )
);
const UserList = lazy(() =>
  import(/* webpackChunkName: "user.list" */ './user.list')
);
const routeList = [
  { path: '/create', element: <NotFoundPage /> },
  { path: '/list', element: <UserList /> },
];

const UserRouter = () => {
  const element = useRoutes(routeList);

  return element;
};

export default UserRouter;
